import { HttpEventType, HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ApiService } from 'src/app/services/api.service';
import { createWorker } from 'tesseract.js';

const worker = createWorker({
  logger: m => console.log(m)
});

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.page.html',
  styleUrls: ['./file-upload.page.scss'],
})
export class FileUploadPage implements OnInit {

  isFile: boolean = false;
  filename: string;
  base64File: any;
  files: any = [];
  percentDone: number;
  uploadSuccess: boolean;
  isUploading: boolean = false;
  uploadingFile: string;
  requestSubscription: Subscription;
  parsedText: any;

  constructor(private router: Router,
    private api: ApiService,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  onFileSelect(e: any, side: any): void {
    try {
      const file = e.target.files[0];
      const fReader = new FileReader()
      fReader.readAsDataURL(file)
      fReader.onloadend = (_event: any) => {
        this.filename = file.name;
        this.base64File = _event.target.result;
        console.log(this.filename);
        this.files.push(file);

        this.imageParser(file);
        this.fileUpload(file);
      }
    } catch (error) {
      this.filename = null;
      this.base64File = null;
    }
  }

  imageParser(image: File) {
    (async () => {
      await worker.load();
      await worker.loadLanguage('eng');
      await worker.initialize('eng');
      const { data: { text } } = await worker.recognize(image);
      this.parsedText = text;
      console.log("Parsed Text", text);
      await worker.terminate();
    })();
  }

  upload() {
    this.isFile = !this.isFile;
  }

  fileUpload(file) {
    this.uploadingFile = file.name;
    this.isUploading = true;
    this.requestSubscription = this.api.uploadAndProgress(file).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.percentDone = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        this.uploadSuccess = true;
        this.isUploading = false;
      }
    });
  }

  deleteFile(item: any) {
    var index = this.files.indexOf(item);
    this.files.splice(index, 1);
    this.cdr.markForCheck();
  }

  cancel(item: any) {
    this.requestSubscription.unsubscribe();
    var index = this.files.indexOf(item);
    this.files.splice(index, 1);
    this.cdr.markForCheck();
  }

}
